console.log("This is a console.log output, like you probably already know.");
setTimeout(
  () =>
    console.table({
      "oh, btw": "did you know",
      "this is cool": "your console can do tables, too!"
    }),
  3000
);

setTimeout(
  () =>
    console.error(
      "Oh, and this is an error, wtf! (Yes, cloudlog can send console.error, too.)"
    ),
  5000
);
